<template>
  <div class="goods-detail pb" @click.stop="menusVisible = false">
    <v-header-goods back v-show="top <= 10">
      <div slot="menus" class="menus">
        <div @click="$router.push({name: 'cart'})" class="menu"><img src="~assets/goods/button_cart_r.png" alt=""></div>
        <div class="menu" @click.stop="menusVisible = !menusVisible"><img src="~assets/goods/button_option.png" alt=""></div>
      </div>
    </v-header-goods>
    <v-header-menus back shadow v-show="top > 10">
      <span class="sub-title" :class="{active: top < offsetTops[1]}" @click="setTop(0)"></span>
      <span class="sub-title" :class="{active: top >= offsetTops[1] && top < offsetTops[2]}" @click="setTop(1)"></span>
      <span class="sub-title" :class="{active: top >= offsetTops[2]}" @click="setTop(2)"></span>
      <div slot="menus" class="menus">
        <div @click="$router.push({name: 'cart'})" class="menu"><img src="~assets/goods/button_cart_g.png" alt=""></div>
        <div class="menu" @click.stop="menusVisible = !menusVisible"><img src="~assets/goods/button_option_g.png" alt=""></div>
      </div>
    </v-header-menus>
    <div class="content" @scroll="scroll">
      <mt-swipe :auto="4000" class="banner">
        <mt-swipe-item v-for="(item, index) in res.bannerList" :key="index">
          <img :src="item" />
        </mt-swipe-item>
      </mt-swipe>
      <div class="info">
        <div class="price">
          <i></i>{{res.price | currency}}
          <img @click="waiting" class="right" src="~assets/goods/button_share.png" alt="">
        </div>
        <div class="name">{{res.goods_title}}</div>
        <div class="desc">{{res.sub_title}}</div>
        <ul class="mark flex">
          <li class="flex" v-if="res.is_new"><img src="~assets/goods/icon_hook_mini.png" alt=""><span></span></li>
          <li class="flex" v-if="res.is_shop_same"><img src="~assets/goods/icon_hook_mini.png" alt=""><span></span></li>
        </ul>
        <div class="courier">
          <span>:{{22 | currency}}</span>
          <span class="right"></span>
        </div>
      </div>
      <div class="gap"></div>
      <div class="row">
        <v-form-slide-up label="" title="" @confirm="handleBenifit">
          <template slot="value">
            <button v-for="card in res.benifit" v-if="card.use" class="benifit-btn">{{card.limit}}{{card.price}}</button>
          </template>
          <ul>
            <li v-for="(card, index) in res.benifit">
              <v-card :card="card"></v-card>
            </li>
          </ul>
        </v-form-slide-up>
      </div>
      <div class="row" v-if="0">
        <v-form-slide-up label="" title="">
          <template slot="value">
            <button class="activity-btn">{{res.activity[0].title}}</button>{{res.activity[0].desc}}
          </template>
          <ul class="activity">
            <li class="flex" v-for="(item, index) in res.activity">
              <button class="activity-btn">{{item.title}}</button>{{item.desc}}
            </li>
          </ul>
        </v-form-slide-up>
      </div>
      <div class="row activity" v-if="res.activity.length > 1">
        <div class="flex" v-for="(item, index) in res.activity" v-if="index > 0">
          <button class="activity-btn">{{item.title}}</button>{{item.desc}}
        </div>
      </div>
      <div class="gap"></div>
      <div class="row">
        <v-form-slide-up label="" v-model="reqData.sku" :placeholder="` ${isZuan ? '' : ''}`" @confirm="handleSKU">
          <ul class="sku">
            <li class="sku-icon flex">
              <img class="icon" src="~assets/goods/pic_guguring.png" alt="">
              <div>
                <div class="price"><span></span>{{8888.00 | currency}}</div>
                <span class="code">{{res.goodsParams.merchant_code}}</span>
              </div>
            </li>
            <li>
              <div class="title">{{isZuan ? '' : ''}}</div>
              <v-button-radio v-model="sku.scoreIndex" :list="res.skuScore"></v-button-radio>
            </li>
            <li>
              <div class="title">{{isZuan ? '' : ''}}</div>
              <v-button-radio v-model="sku.clarityIndex" :list="res.skuClarity"></v-button-radio>
            </li>
            <li>
              <div class="title"></div>
              <v-button-radio v-model="sku.colorIndex" :list="res.skuColor"></v-button-radio>
            </li>
            <li>
              <div class="title"></div>
              <v-button-radio v-model="sku.specIndex" :list="res.skuSpec"></v-button-radio>
            </li>
            <li class="count flex">
              <span></span>
              <div class="flex">
                <div @click="sku.count > 1 && sku.count--" class="btn minus" :class="{active: sku.count > 1}"></div>
                <input v-model="sku.count" type="text" readonly>
                <div @click="sku.count < res.limit && sku.count++" class="btn plus" :class="{active: sku.count < res.limit}"></div>
              </div>
            </li>
          </ul>
        </v-form-slide-up>
      </div>
      <div class="row">
        <v-form-slide-up label="" title="" placeholder="">
          <ul class="goods-param">
            <li class="flex">
              <span class="label"></span>
              <span class="value">{{res.goodsParams.merchant_code}}</span>
            </li>
            <li class="flex">
              <span class="label"></span>
              <span class="value"></span>
            </li>
            <li class="flex">
              <span class="label"></span>
              <span class="value"></span>
            </li>
            <li class="flex">
              <span class="label"></span>
              <span class="value"></span>
            </li>
            <li class="flex">
              <span class="label"></span>
              <span class="value">{{res.goodsParams.zhuzuanxingzhuang}}</span>
            </li>
            <li class="flex">
              <span class="label"></span>
              <span class="value">{{res.goodsParams.fuzuanxingzhuang || ''}}</span>
            </li>
            <li class="flex">
              <span class="label"></span>
              <span class="value">{{res.goodsParams.fuzuanfenshu || ''}}</span>
            </li>
            <li class="flex">
              <span class="label"></span>
              <span class="value">{{res.goodsParams.xiangqiancaizhi}}</span>
            </li>
            <li class="flex">
              <span class="label"></span>
              <span class="value">{{res.goodsParams.xiangqianfangshi}}</span>
            </li>
          </ul>
        </v-form-slide-up>
      </div>
      <div class="gap"></div>
      <div class="row">
        <v-form-slide-up label="" title="" placeholder="">
          <ul class="service">
            <li>
              <div class="flex"><img src="~assets/goods/icon_hook_mini.png" alt=""><span></span></div>
              <span class="note">1</span>
            </li>
            <li>
              <div class="flex"><img src="~assets/goods/icon_hook_mini.png" alt=""><span></span></div>
              <span class="note">CC</span>
            </li>
          </ul>
        </v-form-slide-up>
      </div>
      <div class="row">
        <v-form-slide-up label="" title="" placeholder="" v-model="reqData.lettering" @confirm="handleLettering">
          <ul class="lettering">
            <li class="lettering-enable">
              <div class="title"></div>
              <v-button-radio v-model="lettering.disable" :list="['', '']"></v-button-radio>
            </li>
            <li>
              <div class="title"></div>
              <input v-model="lettering.text" class="lettering-text" type="text" maxlength="2" placeholder="2">
            </li>
            <li>
              <div class="title"></div>
              <input v-model="lettering.remarks" class="lettering-text" type="text" maxlength="20" placeholder="">
            </li>
          </ul>
        </v-form-slide-up>
      </div>
      <div class="gap"></div>
      <div ref="image-text" class="section image-text">
        <div class="title flex"><span></span></div>
        <div class="image-text-content" v-html="res.detail">
          <!-- <img src="~assets/goods/pic_dring.png" alt="">
          <p>CC</p>
          <p>CC
          </p> -->
        </div>
      </div>
      <div class="gap"></div>
      <v-recommend class="section" ref="recommend" title="" :list="res.recommend"></v-recommend>
    </div>
    <div class="footer flex">
      <div class="fun-btns" @click="waiting">
        <img src="~assets/goods/button_service.png" alt="">
        <span></span>
      </div>
      <div class="fun-btns" @click="collect">
        <img src="~assets/goods/button_like.png" alt="">
        <span></span>
      </div>
      <div class="btn-group flex">
        <button class="btn cart" @click="waiting"></button>
        <button class="btn purchase" @click="buyNow"></button>
      </div>
    </div>
    <v-menus v-model="menusVisible" :menus="['home', 'search', 'collect']"></v-menus>
    <v-scroll-top ref="scroll-top" v-model="topVisible" @top="(t)=> top = t"></v-scroll-top>
  </div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';
  import $ from 'jquery';
  import ss from '../assets/goods/pic_guguring.png';
  import banner from '../assets/goods/pic_wring.png';

  export default {
    data() {
      return {
        menusVisible: false,
        topVisible: false,
        top: 0,
        offsetTops: [],
        isZuan: true,
        res: {
          bannerList: [],
          skuScore: [],
          skuClarity: [],
          skuColor: [],
          skuSpec: ['-11', '-12', '-13', '-14', '-15'],
          limit: 10,
          goodsParams: {},
          benifit: [{
            id: 1,
            price: 1500,
            limit: 20000,
            use: false,
            expiredStart: '2018.08.01',
            expiredEnd: '2018.09.01'
          }, {
            id: 2,
            price: 600,
            limit: 1000,
            use: false,
            expiredStart: '2018.08.01',
            expiredEnd: '2018.09.01'
          }],
          activity: [{
            id: 1,
            title: '',
            desc: ''
          }, {
            id: 2,
            title: '',
            desc: ''
          }],

          recommend: [{
            url: ss,
            name: ' ',
            price: '6666',
            like: false
          }, {
            url: ss,
            name: ' ',
            price: '6666',
            like: false
          }, {
            url: ss,
            name: ' ',
            price: '6666',
            like: false
          }, {
            url: ss,
            name: ' ',
            price: '6666',
            like: false
          }]
        },
        sku: {
          scoreIndex: 0,
          clarityIndex: 0,
          colorIndex: 0,
          specIndex: 0,
          count: 1
        },
        lettering: {
          disable: 1,
          text: '',
          remarks: ''
        },
        reqData: {
          sku: '',
          lettering: '',
          benifit: ''
        }
      };
    },
    created() {
      this.fetchGoodsDetail();
    },
    mounted() {
      setTimeout(() => {
        let headerHeight = 96 / window.htp.designWidth * window.screen.width;
        this.offsetTops = [0, Math.trunc(this.$refs['image-text'].offsetTop - headerHeight), Math.trunc(this.$refs['recommend'].$el.offsetTop - headerHeight)];
      }, 1000);
    },
    computed: {
      ...mapGetters(['getCommon'])
    },
    methods: {
      ...mapActions(['ajax']),
      fetchGoodsDetail() {
        this.ajax({
          name: 'goodsDetail',
          id: this.getCommon.goodsId
        }).then(res => {
          Object.assign(this.res, res);
          this.res.bannerList = res.slide_img;
          let skuColor = [];
          let skuScore = [];
          let skuClarity = [];
          let skuSpec = [];

          res.skus.forEach(item => {
            this.isZuan = !!item.zuanshijingdu;
            if(this.isZuan) {
              if(item.zhuzuanfenshu) {
                skuScore.push(item.zhuzuanfenshu);
              }
              if(item.zuanshijingdu) {
                skuClarity.push(item.zuanshijingdu);
              }
            } else {
              if(item.zhushimingcheng) {
                skuScore.push(item.zhushimingcheng);
              }
              if(item.zhushipingji) {
                skuClarity.push(item.zhushipingji);
              }
            }
            if(item.color) {
              skuColor.push(item.color);
            }
            if(item.guige) {
              skuSpec.push(item.guige);
            }
          });
          this.res.skuColor = [...new Set(skuColor)];
          this.res.skuScore = [...new Set(skuScore)];
          this.res.skuClarity = [...new Set(skuClarity)];
          this.res.skuSpec = [...new Set(skuSpec)];
          console.log(this.res);
        });
      },
      handleSKU() {
        let score = this.res.skuScore[this.sku.scoreIndex];
        let clarity = this.res.skuClarity[this.sku.clarityIndex];
        let color = this.res.skuColor[this.sku.colorIndex];
        let spec = this.res.skuSpec[this.sku.specIndex];
        this.reqData.sku = ` ${score}${clarity}${color}${spec}`;
      },
      handleLettering() {
        let disable = this.lettering.disable ? '' : '';
        this.reqData.lettering = this.lettering.disable ? '' : ` ${this.lettering.text} ${this.lettering.remarks}`;
      },
      handleBenifit() {
        this.reqData.benifit = this.res.benifit.map(item => item.id);
      },
      waiting() {
        this.toast('');
      },
      scroll() {
        this.$refs['scroll-top'].scroll();
      },
      setTop(index) {
        $('.content').animate({
          scrollTop: this.offsetTops[index]
        }, 200);
      },
      buyNow() {
        this.$router.push({ name: 'confirmorder' });
      },
      collect() {
        this.ajax({
          name: 'collect',
          data: {
            'collect_id': '5b8d40aa8263913b53665a16'
          }
        }).then(res => {
          this.toast('');
        });
      }
    }
  };
</script>

<style lang="less" scoped>
  .goods-detail {
    background-color: #fff;
  }

  .content {
    padding-bottom: 120px;
  }

  .gap {
    height: 16px;
    background-color: #f3f3f3;
  }

  .sub-title {
    font-size: 30px;
    color: #999;
    padding: 0 20px;
    &.active {
      color: #666;
    }
  }

  .banner {
    height: 600px;
  }

  .info {
    padding: 30px;
    .price {
      color: #cdb49b;
      font-size: 42px;
      span {
        font-size: 30px;
      }
      img {
        width: 40px;
        height: 40px;
        padding: 20px;
      }
    }
    .name {
      padding-top: 30px;
      font-size: 32px;
    }
    .desc {
      padding-top: 18px;
      font-size: 24px;
      color: #999;
    }
    .mark {
      padding-top: 30px;
      li {
        padding-right: 40px;
        font-size: 24px;
        color: #cdb498;
        img {
          width: 24px;
          height: 24px;
          margin-right: 12px;
        }
      }
    }
    .courier {
      padding-top: 30px;
      font-size: 24px;
      color: #666;
    }
  }

  .row {
    height: 84px;
    padding: 0 30px;
    .benifit-btn {
      border-radius: 18px;
      background-color: #fff;
      font-size: 24px;
      padding: 0 14px;
      height: 36px;
      color: #faa0a0;
      border: 1px solid #faa0a0; /*no*/
      margin-left: 24px;
    }

    &.activity {
      padding-top: 10px;
      height: auto;
      font-size: 24px;
      .flex {
        justify-content: flex-end;
        padding-right: 40px;
        padding-bottom: 30px;
      }
    }
  }

  .sku {
    li {
      padding: 20px;
      border-bottom: 1px solid #f0f0f0; /*no*/
      &.sku-icon {
        align-items: flex-end;
        border: none;
        .icon {
          width: 200px;
          height: 200px;
          margin-right: 30px;
        }
        .price {
          color: #cdb49b;
          font-size: 42px;
        }
        .code {
          color: #999;
          font-size: 20px;
        }
      }
      &.count {
        color: #666;
        font-size: 24px;
        justify-content: space-between;
        .btn {
          width: 40px;
          height: 40px;
          &.plus {
            background: url('~assets/goods/button_plus_l.png') no-repeat;
            background-size: 100%;
            &.active {
              background: url('~assets/goods/button_plus_d.png') no-repeat;
              background-size: 100%;
            }
          }
          &.minus {
            background: url('~assets/goods/button_minus_l.png') no-repeat;
            background-size: 100%;
            &.active {
              background: url('~assets/goods/button_minus_d.png') no-repeat;
              background-size: 100%;
            }
          }
        }
        input {
          width: 0;
          min-width: 120px;
          color: #666;
          text-align: center;
          padding: 0 10px;
        }
      }
      .title {
        color: #666;
        font-size: 24px;
      }
    }
  }

  .section {
    &.image-text {
      padding: 0 10px;
      img {
        padding-bottom: 10px;
      }
      p {
        color: #666;
        text-indent: 50px;
        padding: 0 20px 30px 20px;
      }
    }
    .title {
      position: relative;
      height: 84px;
      font-size: 24px;
      color: #ccc;
      width: 100%;
      justify-content: center;
      &:after {
        content: '';
        left: 30px;
        right: 30px;
        height: 1px; /*no*/
        background-color: #f0f0f0;
        display: block;
        top: 50%;
        position: absolute;
      }
      span {
        padding: 0 30px;
        background-color: #fff;
        z-index: 1;
      }
    }
  }

  .footer {
    padding: 0 20px 0 15px;
    height: 96px;
    box-shadow: 0 -10px 50px 10px rgba(170, 170, 170, 0.5);
    .fun-btns {
      padding: 0 43px;
      font-size: 20px;
      color: #999;
      img {
        width: 40px;
        height: 40px;
        display: block;
      }
    }
    .btn-group {
      font-size: 30px;
      height: 68px;
      width: 440px;
      margin-left: auto;
      .btn {
        color: #fff;
        width: 50%;
        height: 100%;
        font-size: 30px;
        &.cart {
          background: url('~assets/goods/button_cart.png') no-repeat;
          background-size: 100% 100%;
        }
        &.purchase {
          background: url('~assets/goods/button_pink_l.png') no-repeat;
          background-size: 100% 100%;
        }
      }
    }
  }
</style>

<style lang="less" scoped>
  /*popup */
  .activity-btn {
    border-radius: 12px;
    background-color: #fff;
    font-size: 16px;
    padding: 0 12px;
    color: #cdb49b;
    border: 1px solid #cdb49b; /*no*/
    margin-right: 8px;
  }

  .goods-param {
    font-size: 24px;
    li {
      height: 84px;
      border-bottom: 1px solid #f0f0f0; /*no*/
      justify-content: space-between;
      padding: 0 20px;
      &:last-child {
        border-bottom: 0;
      }
      .label {
        color: #999;
      }
      .value {
        color: #666;
      }
    }
  }

  .activity {
    li {
      height: 84px;
      font-size: 24px;
      color: #666;
      padding: 0 20px;
      border-bottom: 1px solid #f0f0f0; /*no*/
      &:last-child {
        border: none;
      }
    }
  }

  .service {
    li {
      padding: 20px;
      font-size: 24px;
      color: #666;
      .note {
        padding-left: 36px;
        font-size: 20px;
        color: #999;
      }
      img {
        width: 24px;
        height: 24px;
        margin-right: 12px;
      }
    }
  }

  .lettering {
    li {
      padding: 20px;
      font-size: 24px;
      color: #666;
      .lettering-text {
        height: 84px;
        line-height: 84px;
        font-size: 24px;
        color: #999;
        border-bottom: 1px solid #f0f0f0; /*no*/
        width: 100%;
        margin-top: 20px;
      }
    }
  }
</style>


<style lang="less">
  .goods-detail {
    .lettering-enable {
      button {
        width: 272px;
      }
    }

    .image-text {
      p {
        color: #666;
        text-indent: 50px;
        padding: 0 20px 30px 20px;
      }
    }
  }
</style>




